<template>
    <b-container fluid="lg" id="wrapper">
        <!-- nav -->
        <!-- <ul class="nav nav-tabs">
            <li class="nav-item">
                <router-link class="nav-link" :to="{name : 'mainpage'}">홈화면 <span class="sr-only">(current)</span></router-link>
            </li>
            <li class="nav-item">
                <router-link class="nav-link" :to="{}">일과기록</router-link>
            </li>
            <li class="nav-item">
                <router-link class="nav-link" :to="{name : 'maristore'}">마리상점</router-link>
            </li>
            <li class="nav-item">
                <router-link class="nav-link active" :to="{name : 'collection'}">수집형 포인트</router-link>
            </li>
        </ul> -->

        <b-container fluid="lg" class="row div-collectionInfo">
            <div class="col-12" style="display:flex;">
                <div v-for="(menu, key) in menuTitle" v-bind:key="menu"
                v-on:click="changeMenu(key, menu)"
                class="col-3 div-collection-menu" v-bind:class="{active : selectMenu == key}">{{menu}}</div>
            </div>
            <div class="col-6 div-collectionDocs">
                <div class="div-collectionTitle">
                    <h4>{{menuTitle[selectMenu]}} 획득 경로</h4>
                </div>
                <div class="tab-content" id="nav-tabContent">
                    <div v-for="(collection, key) in collections[selectMenu]" v-bind:key="key" class="tab-pane fade"
                    v-bind:class="{show : selectNumber == key, active : selectNumber == key}"
                    v-bind:id="'list-docs-'+key" role="tabpanel" v-bind:aria-labelledby="'list-collection-'+key">
                    설명+{{key}}
                    </div>
                    <!-- <div class="tab-pane fade" id="list-profile" role="tabpanel"
                        aria-labelledby="list-profile-list">...</div>
                    <div class="tab-pane fade" id="list-messages" role="tabpanel"
                        aria-labelledby="list-messages-list">...</div>
                    <div class="tab-pane fade" id="list-settings" role="tabpanel"
                        aria-labelledby="list-settings-list">...</div> -->
                </div>
            </div>
            <div class="col-6">
                <div class="div-collectionTitle">
                    <h4>{{menuTitle[selectMenu]}} 획득 현황</h4>
                    <!-- <h4>거인의 심장 획득 현황</h4> -->
                </div>
                <div class="list-group div-scrollable" id="list-tab" role="tablist">
                    <div v-for="(collection, key) in collections[selectMenu]" v-bind:key="collection.name"
                    v-on:click="changeSelect(key)"
                    v-bind:class="{active : selectNumber == key,
                    complete : (selectMenu != 'mococo' && collection.complete) || (selectMenu == 'mococo' && collection.userCount == collection.totalCount)}"
                    class="list-group-item list-group-item-action" v-bind:id="'list-collection-'+key" data-toggle="list"
                        role="tab" v-bind:aria-controls="'list-'+key">
                        {{collection.name}}
                        <input type="checkbox" v-if="selectMenu != 'mococo'" class="input-custom-checkbox" v-model="collection.complete">
                        <div v-if="selectMenu == 'mococo'" class="div-collection-count">{{collection.userCount}}/{{collection.totalCount}}</div>
                    </div>
                        <!-- <span v-if="collection.complete">획득</span> -->
                        <!-- <span v-else>미획득</span></a> -->
                </div>
            </div>
        </b-container>


        <p style="color:white">{{collections[selectMenu]}}</p>
    </b-container>
</template>

<script>
    export default {
        data() {
            /*eslint-disable no-unused-labels*/
            return {
                selectNumber : 0,
                //heart, island, mococo, art
                selectMenu : 'heart',
                menuTitle: {
                    'heart' : '거인의 심장',
                    'island' : '섬의 마음',
                    'mococo' : '모코코',
                    'art' : '위대함 미술품'
                    // {"name":"거인의 심장"},
                    // {"name":"섬의 마음"},
                    // {"name":"모코코"},
                    // {"name":"위대한 미술품"}
                },
                collections: {
                    'heart':[],
                    'island':[],
                    'mococo':[],
                    'art':[]
                }
            }
        },
        created() {
            var id = '눈꽃츄르';
            // /%EB%88%88%EA%BD%83%EC%B8%84%EB%A5%B4
            // 해당 정보가 없다면 파싱해와야한다.
            // /api/hearts/user/
            this.$http.get('/api/collections/user/'+id)
                .then((response) => {
                    if (200 == response.status) {
                        let collectionInfo = response.data;
                        console.dir(collectionInfo);
                        for(let key in collectionInfo) {
                            console.dir(collectionInfo[key]);
                            console.log('key>>>'+key);
                            // let jsonObj = JSON.parse(collectionInfo[key]);
                            // this.collections.push(key);
                            for(let key2 in collectionInfo[key]){
                                // console.dir(collectionInfo[key][key2]);
                                this.collections[key].push(collectionInfo[key][key2]);
                            }
                        }
                        // for(let key in this.collections[0]){
                        //     console.dir("collection_f>>>>" + this.collections[0][key].name + " "+ this.collections[0][key].complete);
                        // }

                    } else {
                        console.log('response error : ' + response.status);
                    }
                });
        },
        methods:{
            changeSelect(index){
                this.selectNumber = index;
            },
            changeMenu(key){
                this.selectMenu = key;
                this.selectNumber = 0;
            }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/nav';
    @import '../assets/scss/collection';
</style>