import Vue from 'vue'
import Router from 'vue-router'
import MariStore from '@/components/MariStore'
import Collection from '@/components/Collection'
import Main from '@/components/Main'
import User from '@/components/User'
import CollectionDB from '@/components/CollectionDB'
// import myNav from '@/components/nav'

// import Show from '@/components/MovieShowPage'
Vue.use(Router)
export const router = new Router({
    //mode는 기본으로 hash로써 index.html#/gallery 로 작동되며
    //history <- history API class로 열렸던 페이지를 관리할수있다.
    mode: 'history',
    routes: [{
            path: '/',
            name: 'mainpage',
            component: Main
        },
        {
            path: '/mari',
            name: 'maristore',
            component: MariStore
        },
        {
            path: '/collection',
            name: 'collection',
            component: Collection
        },
        {
            path: '/user',
            name: 'userInfo',
            component: User
        },
        {
            path: '/collectionDB',
            name: 'collectionDB',
            component: CollectionDB
        },
        // {
        //     path: '/:id',
        //     name: 'show',
        //     component: Show
        // }
    ]
})