export const auth = {
    namespaced: true,
    state: {
        isLogin: false,
        //id,대표 캐릭터명
        userInfo: {},
    },
    getters: {
        isLogin(state) {
            return state.isLogin;
        },
        userInfo(state) {
            return state.userInfo;
        }
    },
    mutations: {
        mutateIsLogin(state, isLogin) {
            state.isLogin = isLogin;
        },
        mutateUserInfo(state, userInfo) {
            state.userInfo = { "userId": userInfo.user_id, "userChar": userInfo.user_char };
        },
        mutateRegister(state) {
            state.isLogin = false;
        },
    },
    actions: {
        login(context, { userId, pwd, url }) {
            return new Promise((resolve, reject) => {
                this.$http
                    .post("/user/login", {
                        user_id: userId,
                        user_pwd: pwd
                    })
                    .then(({ data }) => {
                        context.commit("mutateIsLogin", true);
                        //전달 받은 data는 id와 대표캐릭터명만 있다.
                        context.commit("mutateUserInfo", data);

                        resolve({ data })
                    })
                    .catch((error) => {
                        if (error.response) {
                            if (error.response.status == "404") {
                                alertify.error("아이디 또는 비밀번호가 올바르지 않습니다.", 3);
                            } else {
                                alertify.error("로그인 처리시 에러가 발생했습니다.", 3);
                            }
                        }
                        return reject();
                    });
            });
        },
        logout(context) {
            return new Promise((resolve) => {
                context.commit("mutateIsLogin", false);
                context.commit("mutateUserInfo", {});
                return resolve()

                // alertify.error("로그아웃 처리시 에러가 발생했습니다.", 3,
                //     function() { console.log(error.config); });
                // return reject();
            });
        },
        //한명의 유저정보 불러오기 -> 수정 가능한 페이지 
        getUserInfo(context, userid) {
            return new Promise((resolve, reject) => {
                this.$http
                    .get("/user/" + userid)
                    .then(({ data }) => {
                        resolve({ data })
                    })
                    .catch(() => {
                        alertify.error("유저정보가 없습니다.", 3,
                            function() { console.log("유저정보가 없습니다."); });
                        return reject();
                    });
            });
        },
    },
}